@mixin xs {
  @media (max-width: 575.98px) {
    @content;
  }
}

@mixin sm {
  @media (max-width: 767.98px) {
    @content;
  }
}

@mixin md {
  @media (max-width: 991.98px) {
    @content;
  }
}

@mixin lg {
  @media (max-width: 1199.98px) {
    @content;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

::-webkit-scrollbar {
  display: none;
}
